.ant-btn-primary {
  background-color: #009cff;
}

.ant-btn-primary:hover,
.ant-btn-primary:focus {
  color: #fff;
  background-color: #009cff;
  border-color: #009cff;
}

.ant-layout-header {
  background: #001529;
  padding: 0 46px;
  height: 46px;
  line-height: 46px;
}

.ant-table-thead > tr > th,
.ant-table-tbody > tr > td {
  padding: 8px;
}

.ant-form-item {
  margin-bottom: 4px;
}

.ant-form-item-label {
  text-align: left;
}

.ant-tabs-bar {
  width: 100%;
  z-index: 1000;
  background: white;
}

html,
body {
  height: 100%;
  font-size: 14px;
  font-family: sans-serif;
  border-top: 2px solid #263147;
  color: #333;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: sans-serif;
}

#root {
  height: 100%;
  width: 100%;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  display: block;
  height: 50px;
  background-color: #263147;
  padding-left: 24px;
  padding-right: 0;
}

.nav h2 {
  color: white;
  line-height: 50px;
}

.nav .name {
  line-height: 50px;
  padding-right: 10px;
  color: white;
  text-align: right;
  font-size: 16px;
}

.nav .name a,
.nav .name a i {
  color: white;
}

.side-menu {
  height: 100%;
  border-right: 1px solid #e3e3e3;
}

.side-menu .side-button {
  padding: 6px 10px;
  border-bottom: 1px solid #e3e3e3;
}

.side-menu .side-button button {
  width: 100%;
}

.side-menu .side-tree {
  font-weight: bold;
  font-size: 14px;
}

.list-operation {
  text-align: right;
  padding-bottom: 6px;
  border-bottom: 1px solid #e3e3e3;
}

.list-table {
}

.nav-bar {
  position: fixed;
  z-index: 1000;
  width: 100%;
  background: #f0f2f5;
  height: 50px;
  line-height: 50px;
  padding-left: 12px;
}

.content {
  padding: 12px 0;
}

.area {
  padding-left: 24px;
  padding-bottom: 24px;
  width: 700px;
}

.area-title {
  font-size: 20px;
  color: #000;
  margin-bottom: 12px;
  padding-left: 16px;
}

.area-title::before {
  border: 2px solid #f95217;
  content: '';
  position: absolute;
  height: 30px;
  left: 26px;
}

.area-table {
  margin: 10px 0 0;
  width: 100%;
  border: 1px solid #d2d1d2;
}

.area-table tr {
  height: 40px;
}

.area-table tr th {
  width: 200px;
  padding-left: 13px;
  background: #f2f2f2;
  color: #000;
  font-weight: 100;
  font-size: 14px;
}

.area-table tr .required::before {
  content: '\5FC5\987B';
  background: #ff001c;
  color: #fff;
  position: relative;
  right: 5px;
  font-weight: bold;
  padding: 2px 10px;
  font-size: 12px;
}

.area-table tr td {
  padding: 2px 10px;
}

.area-operation {
  width: 100%;
  position: fixed;
  bottom: 6px;
  height: 50px;
  background: white;
  border-top: 1px solid #e3e3e3;
  line-height: 50px;
}

.area-operation button {
  margin-left: 24px;
  width: 120px;
}

.content-history {
  padding: 12px;
}

.steps-def {
  padding: 12px 24px;
}
.steps-content {
  margin-top: 16px;
  border: 1px dashed #e9e9e9;
  border-radius: 6px;
  background-color: #fafafa;
  min-height: 200px;
}

.steps-action {
  margin-top: 24px;
}

.city-items {
  max-height: 400px;
  overflow: auto;
}

.city-items .item {
  padding: 6px 12px;
}

.city-items .item i {
  font-size: 16px;
  padding: 3px 5px;
}

.city-items .item .ant-input {
  width: 350px;
  margin-bottom: 2px;
}

.city-operation {
  padding-left: 16px;
  text-align: center;
}

.city-operation .btn-list {
  margin-top: 8px;
}

.collectors h3 {
  padding-top: 36px;
}

.collectors-filter {
  margin: 0 12px 24px 12px;
}

.collectors-filter .title {
  padding: 6px 12px;
  border-bottom: 1px solid #e6e6e6;
}

.collectors-filter .title .ant-checkbox-wrapper {
  font-size: 18px;
  font-weight: bold;
}

.collectors-filter .ant-form {
  padding: 4px 36px;
}

.max-height {
  height: 100%;
}

.max-width {
  width: 100%;
}

.min-width {
  min-width: 1024px;
}


.footer {
  margin-top: 80px;
}

.footer p {
  text-align: center;
  font-size: 12px;
}

.form-control:focus {
  border-color: #009cff;
}

.login {
  margin-top: 89px;
}
.login .form {
  width: 350px;
  margin: 0 auto;
}
.login label {
  font-weight: normal;
}
.login h1 {
  text-align: center;
  font-size: 46px;
  font-weight: bold;
}

.login input {
  height: 50px;
}

.login .button-disable {
  width: 100%;
  height: 50px;
  background-color: #C0BCBB;
  color: white;
}

.login .button-enable {
  width: 100%;
  height: 50px;
  background-color: #009cff;
  color: white;
  font-size: 16px;
}

.login .button-disabled {
  width: 100%;
  height: 50px;
  font-size: 16px;
}

.login .button-disabled span {
  color: #000;
  opacity: 0.25;
}

.login button:hover {
  background-color: #009cff;
  color: white;
}

.login .logo {
  margin-bottom: 15px;
}

.login .logo img {
  width: 350px;
  height: 78px;
}
